<script>
    import Table, { Pagination, Row, Search, Sort } from "./Table.svelte";
    import { onMount } from 'svelte';
    import {Form, FormGroup, Input, Label, Button, Modal, ModalHeader, ModalBody, ModalFooter} from 'sveltestrap';
    import { sortNumber, sortString } from "./sorting.js";
    import { Circle } from 'svelte-loading-spinners';
    import { invalid_attribute_name_character } from "svelte/internal";


    let page = 0;
    let pageSize = 10;
    let prows = [];
    let selected = null;
    let yes = false;
    let displayTable = false;
    //let newEmp = [...columns];
    let new_emp_id;
    let new_fname;
    let new_lname;
    let new_pri_skill;
    let new_location;
    let new_pic;

    export let load = false;
    $: if (load === true) {
        console.log("Outer run")
        getEmployee();
    }

    //Modal variables
    let open = false;
    const toggle = () => {
        open = !open;
    }

	async function getEmployee() {
        console.log("Inner run")
        let url = '/readEmp'
        const res = await fetch(url).then(res => res.json()).then(
			parsed => {
                    prows = Object.entries(parsed).map(function(entry){
                        let key = entry[0];
                        let value = entry[1];

                        let nested_object = value;
                        nested_object.key = key;
                        
                        return nested_object;
                    });
                displayTable = true
            }	
		);
    }

    function fillRows (eventMsg)  {
        prows = Object.entries(eventMsg.detail).map(function(entry){
                let key = entry[0];
                let value = entry[1];

                let nested_object = value;
                nested_object.key = key;

                return nested_object;
            });
        displayTable = true
    }

    function showTable() {
        
    }

    function onCellClick(row) {
        if(row.quantity !==0) {
            selected = row;

        }
    }

    function onSortString(event) {
        event.detail.rows = sortString(
            event.detail.rows,
            event.detail.dir,
            event.detail.key
        );
    }
    
    async function addEmp(_event){
        confirm("Confirm to add employee?");
        if(new_emp_id !== "" && new_fname !== "" && new_lname !== "" && new_pri_skill !== "" && new_location !== "" && new_pic !== ""){
            
            let formData = new FormData();
            formData.append('emp_id', new_emp_id);
            formData.append('first_name', new_fname);
            formData.append('last_name', new_lname);
            formData.append('pri_skill', new_pri_skill);
            formData.append('location', new_location);

            let file = new File(['emp_pic'], new_pic );

            formData.append('emp_image_file', file, file.name);
            console.log(formData);

            let url = "/addemp"
                const res = await fetch(url, {method: 'POST', body: formData}).then((response) => {
                if (!response.ok){
                    throw new Error('Failed');
                }
            })
            alert("Employee successfully added. ");
            window.location.reload();
            new_emp_id = null;
            new_fname = null;
            new_lname = null;
            new_pri_skill = null;
            new_location = null;
            new_pic = null;
        } 
        else{
            alert("Gentle Reminder: Please ensure all employee details are filled before adding. ");
        }
    }

    async function deleteEmp(row){
        console.log(row.emp_id);
        if(confirm("Confirm to delete employee?") === true){

            let url = "/delEmp?emp_id="+row.emp_id
            const res = await fetch(url).then((response) => {
            if (!response.ok){
                throw new Error('Failed');
            }
            })
        }

        window.location.reload();
    }

    function openModal(){
        open = true;
    }

/*     function showPicture($scope, $timeout){
        AWS.config.update({
            accessKeyId: 
        })
    } */

</script>

{#if displayTable === true}
    <Table {page} {pageSize} rows={prows}  let:rows={rows2}>
        <thead slot="head">
            <tr contenteditable="true">
                <td>                </td>
                <td >
                    Employee ID
                    <Sort key="emp_id" on:sort={onSortString} />
                </td>
                <td>
                    First Name
                    <Sort key="first_name" on:sort={onSortString} /> 
                </td>
                <td>
                    Last Name
                    <Sort key="last_name" on:sort={onSortString} /> 
                </td>
                <td>
                    Position
                    <Sort key="pri_skill" on:sort={onSortString} /> 
                </td>
                <td>
                    Location
                    <Sort key="location" on:sort={onSortString} /> 
                </td>
                <td>
                    Picture
                </td>
                <td> </td>
            </tr>
        </thead>
        <tbody>
        {#each rows2 as row, index (row)}
            <tr on:click={() => onCellClick(row)} class='data'>
                <td><Input type="checkbox" on:change={deleteEmp(row)}/></td>
                <td>{row.emp_id}</td>
                <td>{row.first_name}</td>
                <td>{row.last_name}</td> 
                <td>{row.pri_skill}</td>
                <td>{row.location}</td>
                <!-- <td><Button on:click={showImg} ButtonText = "CALIBRAR"/></td> -->
                <td><button on:click={openModal} class="imgBtn">Show Image</button></td>
                <td> </td>
            </tr>
        {/each}
        <tr>   
                <td> </td>
                <td><Input bind:value={new_emp_id}/></td>
                <td><Input bind:value={new_fname} type="text"/></td>
                <td><Input bind:value={new_lname} type="text"/></td>
                <td><Input bind:value={new_pri_skill} type="text"/></td>
                <td><Input bind:value={new_location} type="text"/></td>
                <td><Input type="file" bind:value={new_pic} style="color:grey"/></td>
                <td>
                    <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" class="bi bi-plus-lg" viewBox="0 0 16 16" on:click={addEmp}>
                        <path fill-rule="evenodd" d="M8 2a.5.5 0 0 1 .5.5v5h5a.5.5 0 0 1 0 1h-5v5a.5.5 0 0 1-1 0v-5h-5a.5.5 0 0 1 0-1h5v-5A.5.5 0 0 1 8 2Z"/>
                      </svg>
                </td>
                <!-- <img class="addImg" type="submit" src="../add.png" alt="An add icon" on:click={addEmp} > -->
        </tr>
        </tbody>
    </Table> 

{:else}
<div class="loading">
    <Circle color='#FF3E00'></Circle>
</div> 
{/if}

<Modal isOpen={open} {toggle}>
    <ModalHeader> Picture of Employee ... </ModalHeader>
    <ModalBody>
        <Form>

                <img src="https://y4-bucket.s3.amazonaws.com/emp-01.jpg" width="100px" height="100px" alt="an employee picture"/>

<!--             <div>
                {{ }}
            </div> -->
        </Form>
    </ModalBody>
    <ModalFooter>
        <Button color="primary" on:click={toggle}>Done</Button>
    </ModalFooter>
</Modal>

<style>
/*     tr.data:hover {
        background-color: lightblue !important;
        transition: 0.2s;
    }  */

    svg {
        cursor: pointer; 
    }

    tr.data {
        background-color: white !important;
        cursor: pointer; 
    }

    tr.base {
        background-color: rgb(245, 245, 245);
    }

    .loading {
        display:flex;
        padding: 1em;
        justify-content: center;
    }

    td{
        text-align: left;
    }

    :global(#qtyRange) {
        border:0px !important;
    }

    
    .addImg{
        height: 3vh;
        cursor: pointer;
    }

    .imgBtn{
        size: 1em;
        padding: 0.2%;

    }

    .imgBtn:hover{
        background-color: darkgrey;
    }

    :global(body.dark-mode) td {
        background-color:  var(--dark-mode-bg) !important;
		color: var(--dark-mode-lines)  !important;
    }

    :global(body.dark-mode .form-control)  {
        background-color:  var(--dark-mode-bg) !important;
		color: var(--dark-mode-lines)  !important;
    }
 

    :global(#qtyRange) {
        border:0px !important;
    }
    

</style>
